          <el-table-column :show-overflow-tooltip="true" property="auditor_time" width="170" label="审核时间"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_status" width="100" label="审核状态">
            <template slot-scope="scope">
              <span v-if="scope.row.auditor_status==1">未审核</span>
              <span v-else-if="scope.row.auditor_status==2">已审核</span>
              <span v-else>作废</span>
