</template>
<script>
import { getData } from "common/utils/public_function/commonUtils";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "aftersaleWarehousingdetail",
  data() {
    return {
      goodsList: [], //商品下拉
      GoodsTire: {}, //查看胎号
      params: {
        tire_type: "1"
      },
      loading:false,
      dialogFormVisible: false,
      dialogFormVisibleSee: false,
      showDepartment: true,
      SelesTomer: {
        options: []
      } //客户列表
    };
  },
  methods: {
    ...mapActions(["tiremanagementSeeACT"]),
    change(val) {
      //选择的日期
      this.params.start_time = val[0];
      this.params.end_time = val[1];
    },
    getCustomerListRemote(query) {
      this.clienteleListInvok(query);
    },
    changeAll(val) {
      this.$set(this.params, "allocation_id", ""); // 初始化货位选中项
    },
    companyChange(val, formName) {
      // 获取部门
      this.$set(this.params, "warehouse_id", ""); // 初始化仓库下拉选中值
      this.$set(this.params, "department_id", ""); // 初始化部门下拉选中值
      this.$set(this.params, "customer_id", ""); // 初始化客户名称下拉选中值
      this.$set(this.params, "allocation_id", ""); // 初始化货位下拉选中值
      // getDepartmentListCommon(this, this[formName], 1);
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    onReset(formName) {
      this.$set(this, "params", {
        tire_type: "1"
      });
      this.timeValue = [];
      this.$set(this, "searchParams", {
        params: {
          tire_type: "1"
        },
        TableData: this.TableData, //列表数据
        API: "getReturnTireDetailsList", //接口名
        vm: this //传递的this
      });
      getData(this.searchParams);
    },
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: 1
      });
      getData(this.searchParams);
    }
  },
  mounted() {
    this.$set(this, "searchParams", {
      params: {}, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getReturnTireDetailsList", //接口名
      vm: this //传递的this
    });
    getData(this.searchParams);
  }
};
</script>

